a.btn{
    display: block;
    background: rgb(94, 93, 93);
}
.scan-btn {
    position: fixed;
    bottom: 0.5rem;
    right: 0.5rem;
    z-index: 999;
    margin-bottom: 1rem;
    margin-right: 0.8rem;
    
}
.scan-btn a {
    border-radius: 48%;
    padding: 22px 16px;
}
.add-btn::after {
    transform: rotate(90deg);
}
.add-btn,
.add-btn::after {
    display: block;
    width: 24px;
    height: 5px;
    position: relative;
    background: rgb(255, 251, 251);
    border-radius: 5px;
}
.add-btn::after {
    content: "";
    position: absolute;
}

